#card-section .header {
    text-align: center;
    font-size: 40px;
    padding-bottom: 60px;
}
.nav-tabs > li > a {
    text-align: center;
    font-size: 20px;
    border-radius: 0 !important;
}
.nav-pills>li.active>a, .nav-pills>li.active>a:focus, .nav-pills>li.active>a:hover {
    background-color: #ffc107;
    border-right: 0;
}
.nav-tabs > li.active::before {
    display: table;
    content: " ";
    right: -28px;
    z-index: 10;
    width: 0;
    position: absolute;
    height: 0;
    border-top: 25px solid transparent;
    border-bottom: 25px solid transparent;
    border-left: 30px solid #ffc107;
}
.list-card {

}
.item-card {
    display: inline-block;
    list-style: none;
    padding: 0;
}
.list-card .item-card img {
    width: 350px;
    padding: 20px;
}
.list-category {
    padding: 0;
    list-style: none;
}
.item-category {
    display: inline-block;
    padding: 10px;
}
.item-category img {
    width: 100%;
}
@media (min-width: 768px) {
    .item-category {
        width: 250px;
    }
}
@media (min-width: 992px) {
    .item-category {
        width: 250px;
    }
}
