    .list-category {
        text-align: center;
    }
    .item-category {
        width: 300px;
    }
}

.list-product {
    list-style: none;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 20px 0;
}
.item-product {
    padding: 10px;
    width: 270px;
}
.item-product img {
    width: 100%;
}
.item-product p {
    padding: 0;
    margin: 0;
}

.item-product input[type=radio] {
    display: none;
}

.list-product img {
    height: 150px;
    width: 240px;
    border: 5px solid transparent;
}

.item-product.selected label {
    position: relative;
}

.item-product.selected label:before {
    font-family: 'FontAwesome';
    font-size: 40px;
    content: "\f00c";
    position: absolute;
    color: #FFEB3B;
    background-color: rgba(0,0,0,0.4);
    width: 100%;
    height: 100%;
    line-height: 3.5;
    border: 5px solid #FFEB3B;
}

footer {
    padding: 20px 0;
    text-align: center;
    height: 150px;
    background-repeat: no-repeat;
    background-attachment: scroll;
